import React, { Component } from 'react';
import emailjs from 'emailjs-com';
import { Form, FormLabel } from 'react-bootstrap';
import { withTranslation } from 'react-i18next';


function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('gmail', 'template_0GCXxagz', e.target, 'user_dNokYX5Ebo1aMAvjNj39O')
      
      .then((result) => {
        console.log(result.text);
        document.getElementById('email-success').classList.remove('d-none');
        document.getElementById('email-error').classList.add('d-none');
      }, (error) => {
          document.getElementById('email-error').classList.remove('d-none');
          document.getElementById('email-success').classList.add('d-none');
          console.log(error);
      });
  }
class ContactUs extends Component {
    render() {
        const { t } = this.props;
        return (
            <div class="w-100">
              <p class="p-3"> {t("contactLabel")}</p>
              <Form className="contact-form" onSubmit={sendEmail}>
      
              <div class="col-12 mt-4 form-group">
              <div id="email-success" class="d-none bg-success alert-success"> {t("contactWithSuccess")}</div>
              <div id="email-error" class="d-none bg-danger alert-danger"> {t("contactWithError")}</div>
      
                  <input type="hidden" name="contact_number" class="pt-3" />
                  <FormLabel>{t("contactName")}</FormLabel>
                  <input class="form-control input" type="text" name="name" required/>
              </div>
      
              <div class="col-12 form-group">
                  <FormLabel>{t("contactEmail")}</FormLabel>
                  <input class="form-control input" type="email" name="email" required/>
              </div>
      
              <div class="col-12 form-group">
                  <FormLabel>{t("contactMessage")}</FormLabel>
                  <textarea class="form-control form-text" name="message" minLength="10" required/>
              </div>
      
              <div class="col-12 form-group">
                  <input class="btn btn-block btn-outline-success" type="submit" value={t("contactSend")} />
              </div>
      
              
              </Form>
            </div>
        );
    }
}

export default withTranslation()(ContactUs)