import React, { Component } from 'react';
import {Button} from 'react-bootstrap';
import i18n from './i18n';
import { withTranslation } from 'react-i18next';

class languageSwitcher extends Component {
  state = {
    value: "en"
  };
  handleChange = event => {
    console.log("selected val is ", event.target.value);
    let newlang = event.target.value;
    this.setState(prevState => ({ value: newlang }));
    console.log("state value is", newlang);
    i18n.changeLanguage(newlang);
  };

  render(){
    const { t } = this.props;
    return (
      <div class="row">
        <Button className="col-sm-4 bg-transparent button-transparent btn-language-switcher" onClick={this.handleChange} value="en">{t('en')} 
        <img src="https://res.cloudinary.com/cinforme/image/upload/v1582388051/BLOG/en.png" alt="English flag"/> 
        </Button>
        <Button className="col-sm-4 bg-transparent btn-language-switcher"  onClick={this.handleChange} value="fr">{t('fr')} 
        <img src="https://res.cloudinary.com/cinforme/image/upload/v1582388051/BLOG/fr.png" alt="French flag"/>
        </Button>
        <Button className="col-sm-4 bg-transparent btn-language-switcher"  onClick={this.handleChange} value="pt">{t('pt')} 
        <img src="https://res.cloudinary.com/cinforme/image/upload/v1582388051/BLOG/pt.png" alt="Portuguese flag"/>
        </Button>
      </div>
    )
  }
}

export default withTranslation()(languageSwitcher);